.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 35px;
  padding-left: 35px;
}

.App-header {
  background-color: #1D428A;
  display: flex;
  align-items: center;
  justify-content: left;
  height: 50px;
  padding-left: 35px;
  width: 100%;
}

@charset "UTF-8";

#shot-chart {
  width: 100%;
  margin: auto;
  margin-top: -50px;
}

/********SHOTCHART COURT STYLES****** */
#shot-chart-container {
  margin-top: -22%;
  margin-bottom: 5%;
}

@media (min-width: 50em) {
  #shot-chart-container {
    margin-top: -15%;
  }
}

@media (min-width: 55em) {
  #shot-chart-container {
    margin-top: -10%;
  }
}

@media (min-width: 78em) {
  #shot-chart-container {
    padding-left: 1em;
    margin-left: 33%;
  }
}

#shot-chart-container .shot-chart {
  display: block;
  margin-left: auto;
  margin-right: auto;
}

@media (min-width: 78em) {
  #shot-chart-container .shot-chart {
    margin: 0;
  }
}

svg {
  stroke-width: .2;
}

svg text {
  font-size: 0.2em;
}

svg text.legend-text {
  font-size: 0.1em;
}

svg rect {
  stroke: black;
  fill: none;
}

svg line {
  stroke: black;
  fill: none;
}

/********END SHOTCHART COURT STYLES****** */
/********SHOT STYLES****** */
svg circle {
  stroke: black;
  fill: none;
}

svg circle.shot.make {
  fill: green;
  stroke: white;
  stroke-width: 0.05;
}

svg circle.shot.miss {
  fill: red;
  stroke: white;
  stroke-width: 0.05;
}

path {
  stroke: white;
  stroke-width: 0.05;
}

path.shot {
  stroke: white;
  fill: none;
  stroke-width: 0.05;
}

/********END SHOT STYLES****** */
/********SHOT TOOLTIPS****** */
.d3-tip {
  line-height: 1;
  font-weight: lighter;
  padding: 12px;
  background: #006886;
  color: #fff;
  border-radius: 2px;
  pointer-events: none;
}

/* Creates a small triangle extender for the tooltip */
.d3-tip:after {
  box-sizing: border-box;
  display: inline;
  font-size: 10px;
  width: 100%;
  line-height: 1;
  color: #006886;
  position: absolute;
  pointer-events: none;
}

/* Northward tooltips */
.d3-tip.n:after {
  content: "\25BC";
  margin: -1px 0 0 0;
  top: 100%;
  left: 0;
  text-align: center;
}

/* Eastward tooltips */
.d3-tip.e:after {
  content: "\25C0";
  margin: -4px 0 0 0;
  top: 50%;
  left: -8px;
}

/* Southward tooltips */
.d3-tip.s:after {
  content: "\25B2";
  margin: 0 0 1px 0;
  top: -8px;
  left: 0;
  text-align: center;
}

/* Westward tooltips */
.d3-tip.w:after {
  content: "\25B6";
  margin: -4px 0 0 -1px;
  top: 50%;
  left: 100%;
}

.profile {
  width: 200px;
  background: #ccc;
  font-size: 0.8em;
  text-align: center;
}

.profile div {
  box-sizing: border-box;
}

.profile-entry {
  display: flex;
  align-items: center;
  width: 100%;
  padding: 10px 10px 10px;
}

.profile-entry-left {
  text-align: left;
  font-weight: bold;
}

.profile-entry-right {
  text-align: right;
  flex: 1 1;
}

.profile-pic {
  width: 100%;
}

.team-logo {
  width: 50%;
}

.player-name {
  font-size: 20px;
  font-weight: bold;
}

.main {
  width: 750px;
  margin: 70px auto auto;
}

.player {
  display: flex;
  margin-left: auto;
  margin-right: auto;
  border: 3px solid #ccc;
  /*box-shadow: 5px 10px 10px #888;*/
  border-radius: 10px;
  overflow: hidden;
}

.data-view {
  width: 550px;
  position: relative;
}

.filter-label {
  text-align: left;
  line-height: 37px;
}

.filters {
  position: absolute;
  width: 100%;
  bottom: 0;
}

.filter-row {
  margin-bottom: 20px;
}

.filter-control {
  float: left;
}
.search-bar {
  width: 100%;
  margin-bottom: 30px;
  font-family: "Ubuntu Light", sans-serif;
}

.player-option-image {
  height: 32px;
  margin-right: 20px;
}

.player-option-label {
  font-size: 15px;
  font-family: "Ubuntu Light", sans-serif
  /*font-family: "Product Sans", sans-serif*/
}
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Ubuntu", "Segoe UI", "Roboto", "Oxygen",
    "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
